import PropTypes from 'prop-types';
// @mui
import { useTheme, styled } from '@mui/material/styles';
import { Input, Toolbar, Tooltip, MenuItem, InputLabel,IconButton, Select, Typography, InputAdornment, TextField } from '@mui/material';
import DatePicker from "@mui/lab/DatePicker";

// components
import Iconify from '../../../../components/Iconify';
import InputStyle from '../../../../components/InputStyle';

// ----------------------------------------------------------------------

const RootStyle = styled(Toolbar)(({ theme }) => ({
  height: 96,
  display: 'flex',
  justifyContent: 'space-between',
  padding: theme.spacing(0, 1, 0, 3),
}));

const DateStyle = styled('div')(({ theme }) => ({
  width: 450,
  // padding: theme.spacing(0, 1, 0, 3),
  display: 'flex',
  justifyContent: 'space-between',
}));

// ----------------------------------------------------------------------

ProductListToolbar.propTypes = {
  numSelected: PropTypes.number,
  filterName: PropTypes.string,
  onFilterName: PropTypes.func,
  onDeleteProducts: PropTypes.func,
  startDate: PropTypes.instanceOf(Date),
  endDate: PropTypes.instanceOf(Date),
  selecteStartdDate: PropTypes.func,
  selecteEnddDate: PropTypes.func,
  clearDate: PropTypes.func,
};

export default function ProductListToolbar({ numSelected, filterName, onFilterName, onDeleteProducts, startDate, endDate, selecteStartdDate, selecteEnddDate, clearDate,placeholder,enable }) {
  const theme = useTheme();
  const isLight = theme.palette.mode === 'light';

  return (
    <RootStyle
      sx={{
        ...(numSelected > 0 && {
          color: isLight ? 'primary.main' : 'text.primary',
          bgcolor: isLight ? 'primary.lighter' : 'primary.dark',
        }),
      }}
    >

      {numSelected > 0 ? (
        <Tooltip title="Delete">
          <IconButton onClick={onDeleteProducts}>
            <Iconify icon={'eva:trash-2-outline'} />
          </IconButton>
        </Tooltip>
      ) : (
      
        
!enable && <DateStyle >
  <DatePicker
    label="From"
    value={startDate}
    onChange={(date) => selecteStartdDate(date)}
    maxDate={!endDate ? null : new Date(endDate)}
    renderInput={(params) => (
      <TextField sx={{ mr: 1 }}    {...params}
      />
    )}
  />
  <DatePicker
    label="To"
    value={endDate}
    onChange={(date) => selecteEnddDate(date)}
    minDate={!startDate ? null : new Date(startDate)}
    renderInput={(params) => (
      <TextField   {...params}
      />
    )}
  />
  
   
  <Tooltip title="Restart" sx={{
    width: 36, height: 36, ml: 1, mt: 1, color: isLight ? 'primary.main' : 'text.primary',
    bgcolor: isLight ? 'primary.lighter' : 'primary.dark',
  }}>
    <IconButton onClick={() => clearDate()}>
      <Iconify icon={'ic:round-restart-alt'} sx={{ fontSize: 22 }} />
    </IconButton>
  </Tooltip>
</DateStyle> 

      )}


      {numSelected > 0 ? (
        <Typography component="div" variant="subtitle1">
          {numSelected} selected
        </Typography>
      ) : (
        <InputStyle
          stretchStart={240}
          value={filterName}
          onChange={(event) => onFilterName(event.target.value)}
          placeholder={placeholder}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <Iconify icon={'eva:search-fill'} sx={{ color: 'text.disabled', width: 20, height: 20 }} />
              </InputAdornment>
            ),
          }}
        />
      )}



    </RootStyle>
  );
}
