export default function CompanyList() {


  const { themeStretch } = useSettings();
  // const theme = useTheme();
  // const isLight = theme.palette.mode === 'light';
  // const dispatch = useDispatch();

  const [productList, setProductList] = useState([]);
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState('asc');
  const [selected, setSelected] = useState([]);
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [orderBy, setOrderBy] = useState('');
  const [count, setCount] = useState(0);
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);
  // const [anchorEl, setAnchorEl] = useState(null);
  const [selectedIndex, setSelectedIndex] = useState(1);
  const [selectedProduct, setSelectedProduct] = useState([]); 
  const [invoiceDetails, setInvoiceDetails] = useState()
  // const open = Boolean(anchorEl);

  useEffect(() => { 
    getdata()
  }, [rowsPerPage, page, filterName, startDate, endDate ]);

  const handleRequestSort = (property) => {
    // console.log("property",property );
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
  };

 
  const handleClick = (name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
    }
    setSelected(newSelected);
  };

  const handleChangeRowsPerPage = async (event) => {
    console.log(event.target.value);
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handlePageChange = (value) => {
    setPage(value)
  }


  const handleFilterByName = async (filterName) => {
    setFilterName(filterName);
  };

 
  
  const getdata = async (event) => {
    const skip = page * rowsPerPage;
    const limit = rowsPerPage;
    console.log('page', page);
    console.log('skip', skip);
    console.log('limit', limit);
    // console.log('filterStatus', filterStatus);
    
    console.log('startDate,endDate', startDate, endDate);
    const value = await getcompany(skip, limit, filterName, startDate, endDate);
    if(value){
    setProductList(value.data);
    setCount(value.count);
    setInvoiceDetails( value.totalDocData)}
    // setPage(0);
  };

  const PAID_STATUS = ['UnPaid', 'Paid', 'Over Due', 'Cancelled'];

  const handleDeleteProduct = (productId) => {
    const deleteProduct = productList.filter((product) => product.id !== productId);
    setSelected([]);
    setProductList(deleteProduct);
  };

  const handleDeleteProducts = (selected) => {
    const deleteProducts = productList.filter((product) => !selected.includes(product.name));
    setSelected([]);
    setProductList(deleteProducts);
  };

  const handleclearDate = () => {
    setStartDate(null);
    setEndDate(null);
  }


  function stringToColor(string) {
    let hash = 0;
    let i;
  
    /* eslint-disable no-bitwise */
    if(string){
    for (i = 0; i < string.length; i += 1) {
      hash = string.charCodeAt(i) + ((hash << 5) - hash);
    }}else{
      hash=''
    }
  
    let color = '#';
  
    for (i = 0; i < 3; i += 1) {
      const value = (hash >> (i * 16)) & 0xff;
       color += `00${value.toString(16)}`.slice(-2);
    }
     
    /* eslint-enable no-bitwise */
  
    return color;
  }
    function stringAvatar(name) {
      return {
        sx: {
          bgcolor: stringToColor(name),
          mr: 2
        },
        children: name ? `${name.split(' ')[0][0]}${name.split(' ')[0][1]}` : '',
      };
    }
  
  
  // const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - productList.length) : 0;
  const emptyRows = !productList.length && Boolean(!filterName);
  // const filteredProducts = applySortFilter(productList , getComparator(order, orderBy), filterName );
  // const filteredProducts = saerchFilter(  filterName );

  const isNotFound = !productList.length && Boolean(filterName);


 
 
  return (
    <Page title=" Company List">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Company List"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            {
              name: 'Company',
              href: PATH_DASHBOARD.company.root,
            },
            { name: 'Company List' },
          ]} action={<Button
            variant="contained"
            component={RouterLink}
            to={PATH_DASHBOARD.company.newcompany}
            startIcon={<Iconify icon={'eva:plus-fill'} />}
          >
            New Company
          </Button>}
        />

    

        <Card>
      

          <ProductListToolbar
            numSelected={selected.length}
            filterName={filterName}
            clearDate={() => handleclearDate()}
            onFilterName={handleFilterByName}
            onDeleteProducts={() => handleDeleteProducts(selected)}
            startDate={startDate}
            endDate={endDate}
            enable={!false}
            placeholder="Search Companies"
            selecteStartdDate={(value) => setStartDate(value)}
            selecteEnddDate={(value) => setEndDate(value)}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table>
                <ProductListHead
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={productList.length}
                  numSelected={selected.length}
                  onRequestSort={handleRequestSort}
                // onSelectAllClick={handleSelectAllClick}
                />

                <TableBody>
                  {/*  {productList.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => { */}
                  {productList.map((row) => {
                   
                    const { _id, name,phone,address ,createsAt} = row;
                    const isItemSelected = selected.indexOf(_id) !== -1;

                    return (

                      <TableRow
                        hover
                        key={_id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                       <TableCell padding="checkbox">
                          <Checkbox checked={isItemSelected} onClick={() => handleClick(_id)} />
                        </TableCell>
                        <TableCell sx={{ display: 'flex', alignItems: 'center' }}>
                          {/* <Avatar alt={name} src={avatarUrl} sx={{ mr: 2 }} /> */}
                          <Avatar {...stringAvatar(name) } />
                          <Typography variant="subtitle2" noWrap>
                            {name}
                          </Typography>
                        </TableCell>
                        {phone && <TableCell >{phone}</TableCell>}
                        {!phone&& <TableCell > ---- </TableCell>}
                        {address&& <TableCell > {address} </TableCell>}
                        {!address&& <TableCell > ---- </TableCell>}
                        {/* <TableCell >{fDate(createsAt?createsAt:newDate)} </TableCell> */}

                      {/*  <TableCell >{companyPhone}</TableCell>
                        <TableCell style={{ minWidth: 160 }}>{fDate(invoiceDate)}</TableCell>
                        <TableCell >{fCurrency(grandTotal)}</TableCell>
                        <TableCell >{fDate(invoiceDate + (due * (1000 * 60 * 60 * 24 * 30)))} </TableCell>
                        <TableCell >

                          <Label
                            variant={isLight ? 'ghost' : 'filled'}
                            onClick={(e) => handleClickListItem(e, _id)}
                            color={
                              (paidStatus === 'Paid' && 'success') || (paidStatus === 'UnPaid' && 'info') || (paidStatus === 'Cancelled' && 'warning') || 'error'
                            }
                          >
                            {sentenceCase(paidStatus)}
                          </Label>

                         
                        </TableCell> */}
                        <TableCell align="right">
                          <ProductMoreMenu menu="company" productId={_id} onDelete={() => handleDeleteProduct(_id)} />
                        </TableCell>
                        {/* <TableCell align="right">{}</TableCell> */}

                      </TableRow>
                    );
                  })}
                  {emptyRows > 0 && (
                    <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell align="center" colSpan={8} >No Record</TableCell>

                    </TableRow>
                  )}
                </TableBody>

                {isNotFound && (
                  <TableBody>
                    <TableRow>
                      <TableCell align="center" colSpan={8}>
                        <Box sx={{ py: 3 }}>
                          <SearchNotFound searchQuery={filterName} />
                        </Box>
                      </TableCell>
                    </TableRow>
                  </TableBody>
                )}
              </Table>
            </TableContainer>
          </Scrollbar>

          <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={count}
            rowsPerPage={rowsPerPage}
            page={page}
            // onPageChange={(event, value) => { setPage(value) }}
            onPageChange={(event, value) => { console.log('onPageChange', value); handlePageChange(value) }}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Card>
      </Container>
    </Page>
  );
}

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function saerchFilter(search) {
  console.log(search);
}


function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });

  if (query) {
    console.log('----', query);

  }

  return stabilizedThis.map((el) => el[0]);
}
